.about header {
  padding-bottom: 30px;
}
.about h1 {
  text-align: center;
}
.about-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.about-container {
  margin: 0 80px;
}

.about-icon {
  flex: 1;
  text-align: center;
}

.about-icon img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
}

.about-detail {
  padding: 0 75px;
  flex: 2;
  text-align: left;
}
