.header {
  background: url('../img/header-background.jpg');
  padding: 30px 75px;
  color: #8c8c8c;
}

.nav-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav-item {
  position: relative;
  width: 100px;
  height: 50px;
}

.nav-item a {
  color: #8c8c8c;
  text-decoration: none;
}

.nav-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 50px;
}

.nav-link::before,
.nav-link::after,
.nav-title::before,
.nav-title::after {
  background-color: #8c8c8c;
  content: '';
  display: block;
  position: absolute;
  z-index: 10;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
}

/* 左上へ配置 */
.nav-link::before {
  width: 0px;
  height: 1px;
  top: -1px;
  left: -1px;
}

/* 右下へ配置 */
.nav-link::after {
  width: 0px;
  height: 1px;
  right: -1px;
  bottom: -1px;
}

/* 左下へ配置 */
.nav-title::before {
  width: 1px;
  height: 0px;
  left: -1px;
  bottom: -1px;
}
/* 右下へ配置 */
.nav-title::after {
  width: 1px;
  height: 0px;
  top: -1px;
  right: -1px;
}

/* hover */
.nav-link:hover::before,
.nav-link:hover::after {
  width: 100%;
  width: calc(100% + 1px);
}

.nav-title:hover::after,
.nav-title:hover::before {
  height: 100%;
  height: calc(100% + 1px);
}
